<template>
  <div>
    <div class="p-step_mypage__list-box">
      <a :href="'/step/ditail/' + step.id" class="p-step_mypage__card" v-for="(step, index) in steps" :key="index">
        <div>
          <div class="p-step_mypage__thead">
            <img :src="'data:image/png;base64,' + step.pic" alt="STEPTOP" class="p-step_mypage__img" v-if="step.pic">
            <img src="/imges/no_image.png" alt="STEPTOP" class="p-step_mypage__img" v-else>
          </div>
          <div class="p-step_mypage__tbody">
            <div class="p-step_mypage__top">
              <p>{{ formatDate(step.created_at) }}</p>
              <div class="u-flex">
                <div class="u-flex">
                  <i class="fas fa-inbox"></i>
                  <p class="u-ml5">{{ step.category.name }}</p>
                </div>
                <div class="u-flex u-ml10">
                  <i class="fas fa-hourglass-end"></i>
                  <p class="u-ml5">{{ step.total_time }}</p>
                </div>
              </div>
            </div>
            <div class="p-step_mypage__medium">
              <p class="u-mb5">STEP</p>
              <p class="p-step_mypage__medium__font">{{ step.title}}</p>
            </div>
            <div class="p-step_mypage__bottom">
            <img :src="'data:image/png;base64,' + step.user.pic" alt="" class="p-step_mypage__icon" v-if="step.user.pic">
              <img src="/imges/no_image.png" alt="" class="p-step_mypage__icon" v-else>
            <p class="u-ml10" v-if="step.user.name == null"></p>
            <p class="u-ml10" v-else>{{ step.user.name }}</p>
            </div>
          </div>
        </div>
      </a>
    </div>
    <!--  -->
    <div v-if="paginate.total > 12">
    <pagination-component :data="paginate" @move-page="movePage($event)"></pagination-component>
    </div>
  </div>
</template>
<script>
export default {
  props: ['search','category'],
  data: function(){
    return {
      steps: {},
      paginate: {},
      page: 1,
    }
  },
  mounted() {
    this.fetchList()
  },
  methods:{
    // step
    fetchList(){
      const url = '/api/step/mypage_register';
      let param = '';
      if (this.search !== '' && this.category !== '') { // searchcategory
        param += '?search=' + encodeURI(this.search) + '&category_id=' + this.category + '&page=' + this.page
      }else if(this.search !== '' && this.category === ''){ // search
        param += '?search=' + encodeURI(this.search) + '&category_id=' + '&page=' + this.page
      }else if(this.search === '' && this.category !== ''){ // category
        param += '?search=' + '&category_id=' + this.category + '&page=' + this.page
      }else{ // 
        param = '?page=' + this.page;
      }
      axios.get(url + param).then(response => { 
        this.steps = response.data.data
        this.paginate = response.data
      })
    },
    formatDate: function(date){
      // YYYY/MM/DD
      if(!date) return '-';
      return moment(date).format('YYYY/MM/DD');
    },
    movePage(page) {
      this.page = page
      this.fetchList()
      scrollTo(0, 0);
    },
  }
}
</script>